import type { ForwardedRef } from "react";
import React, { useEffect, useRef } from "react";
import type { Control } from "react-hook-form";
import { useFormContext } from "react-hook-form";
import { get } from "../../utils.js";
import { useStringInputController } from "../hooks/useStringInputController.js";
import { multiref } from "../hooks/utils.js";

interface MDXEditorMethods {
  setMarkdown: (markdown: string) => void;
}

interface AutoRichTextInputProps {
  field: string;
  control?: Control<any>;
  editorRef?: ForwardedRef<MDXEditorMethods> | null;
}

const AutoRichTextInput = React.lazy(async () => {
  const [
    {
      MDXEditor,
      BoldItalicUnderlineToggles,
      ListsToggle,
      CodeToggle,
      CreateLink,
      headingsPlugin,
      listsPlugin,
      quotePlugin,
      thematicBreakPlugin,
      markdownShortcutPlugin,
      linkDialogPlugin,
      diffSourcePlugin,
      toolbarPlugin,
      DiffSourceToggleWrapper,
      UndoRedo,
      BlockTypeSelect,
      Separator,
    },
    _,
  ] = await Promise.all([import("@mdxeditor/editor"), import("@mdxeditor/editor/style.css")]);

  const RichTextInput: React.FC<AutoRichTextInputProps> = (props) => {
    const { formState } = useFormContext();
    const { field, control, editorRef, ...rest } = props;
    const controller = useStringInputController({ field, control });
    const innerRef = useRef<MDXEditorMethods | null>(null);

    useEffect(() => {
      if (innerRef.current) {
        innerRef.current.setMarkdown(controller.value?.markdown ?? "");
      }
    }, [controller.value]);

    return (
      <MDXEditor
        plugins={[
          headingsPlugin(),
          listsPlugin(),
          quotePlugin(),
          thematicBreakPlugin(),
          markdownShortcutPlugin(),
          linkDialogPlugin(),
          diffSourcePlugin({
            diffMarkdown: get(formState.defaultValues, field)?.markdown ?? "",
            viewMode: "rich-text",
            readOnlyDiff: true,
          }),
          toolbarPlugin({
            toolbarContents: () => (
              <DiffSourceToggleWrapper>
                <UndoRedo />
                <BlockTypeSelect />
                <Separator />
                <BoldItalicUnderlineToggles />
                <ListsToggle />
                <CodeToggle />
                <CreateLink />
              </DiffSourceToggleWrapper>
            ),
          }),
        ]}
        contentEditableClassName="autoform-prose"
        markdown={controller.value?.markdown ?? ""}
        onChange={(markdown: any) => controller.onChange({ markdown })}
        ref={multiref(innerRef, editorRef) as any}
        {...rest}
      />
    );
  };

  return { default: RichTextInput };
});

export default AutoRichTextInput;
